@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;500;800&display=swap);
@import url(https://cdn.jsdelivr.net/npm/@splidejs/splide@latest/dist/css/splide.min.css);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: var(--main-text-color);
  font-family: 'Montserrat', sans-serif;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.heading {
    font-size: 1rem;
    font-weight: 500;
}
.sub-heading {
    font-size: 1.5rem;
    font-weight: 500;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.details-container {
    background-color: #c4c4c480;
    width: 185px;
    display: flex;
    justify-content: space-between;
    padding: 0.5rem;
}
.details-intro {
    display: flex;
    flex-direction: column;
    text-align: left;
    width: 70%;
}
.title {
    font-weight: 800;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.release-date {
    font-size: 15px;
    font-weight: 400;
}
.rating {
    display: flex;
    justify-content: center;
    align-items: center;
}
.splide__pagination {
    display: none;
}
nav {
    background-color: var(--main-theme-color);
    display: flex;
    padding: 1.5rem;
    justify-content: space-between;
    align-items: center;
}
#logo-title {
    text-transform: uppercase;
    color: var(--main-text-color);
    font-size: 18px;
    font-weight: 500;
}
@media only screen and (max-width: 520px) {
    nav {
        flex-direction: column;
    }
    #logo-title {
        margin-bottom: 1rem;
    }
}
input{
    border: 0;
    padding: 0.5rem;
    width: 15rem;
}
input:focus {
    outline: 0;
}
input::-webkit-input-placeholder {
    font-style: italic;
    font-size: 10px;
}
input:-ms-input-placeholder {
    font-style: italic;
    font-size: 10px;
}
input::placeholder {
    font-style: italic;
    font-size: 10px;
}

.search-box {
    display: flex;
}
.magnifying-glass-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    padding-right: 0.5rem;
}
.magnifying-glass {
    height: 15px;
}
.magnifying-glass-container:hover {
    cursor: pointer;
}

.slider {
    margin-top: 1rem;
}
.local-heading {
    text-align: left;
    padding: 0.5rem 1.5rem;
}

.local-sub-heading {
    margin-top: 1rem;
    text-align: left;
    padding-left: 1.5rem;
}
