.details-container {
    background-color: #c4c4c480;
    width: 185px;
    display: flex;
    justify-content: space-between;
    padding: 0.5rem;
}
.details-intro {
    display: flex;
    flex-direction: column;
    text-align: left;
    width: 70%;
}
.title {
    font-weight: 800;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.release-date {
    font-size: 15px;
    font-weight: 400;
}
.rating {
    display: flex;
    justify-content: center;
    align-items: center;
}